const Name = ({myName, myAge, myAdress}) => {
    return (
        <React.Fragment>
        <p>{`Olá ${myName}`}</p>
        <p>{`A minha idade não é ${myAge}`}</p>
       <p>{`Eu não moro no ${myAdress}`}</p>
       </React.Fragment>
       ) 
}

